<template>
  <main v-if="productLoading">
    <preloader/>
  </main>
  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            {{ categories.title }}
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            {{ product.title }}
          </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img width="570" height="570" :src="product.image.file.url" :alt="product.name">
        </div>
      </div>

      <div class="item__info">
        <span class="item__code">: {{ product.id }}</span>
        <h2 class="item__title">
          {{ product.title }}
        </h2>
        <div class="item__form">
          <form class="form" action="#" method="POST" @submit.prevent="addToCart">
            <b class="item__price">
              {{ product.price | numberFormat }} 
            </b>
            <fieldset class="form__block">
              <legend class="form__legend"></legend>
              <colors-list :colors="product.colors" :currentColor.sync="currentColor"/>
            </fieldset>

            <fieldset class="form__block">
              <legend class="form__legend">  :</legend>

              <ul class="sizes sizes--primery">
                <li class="sizes__item">
                  <label class="sizes__label">
                    <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="32">
                    <span class="sizes__value">
                      32gb
                    </span>
                  </label>
                </li>
                <li class="sizes__item">
                  <label class="sizes__label">
                    <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="64">
                    <span class="sizes__value">
                      64gb
                    </span>
                  </label>
                </li>
                <li class="sizes__item">
                  <label class="sizes__label">
                    <input class="sizes__radio sr-only" type="radio" name="sizes-item" value="128" checked="">
                    <span class="sizes__value">
                      128gb
                    </span>
                  </label>
                </li>
              </ul>
            </fieldset>

            <div class="item__row">
              <ProductCount :productAmount.sync="productAmount"/>
              <button class="button button--primery" type="submit" :disabled="productAddSending">
                 
              </button>
              <div v-if="productAdded">   </div>
              <div v-if="productAddSending"> ...</div>
            </div>
          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a class="tabs__link tabs__link--current">
              
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
                
            </a>
          </li>
        </ul>

        <div class="item__content">
          <p>
             GPS, , BEIDOU Galileo  QZSS<br>
              <br>
              Bluetooth Smart, ANT+  Wi-Fi<br>
              <br>
          </p>

          <a href="#">
             
          </a>

          <h3> ?</h3>

          <p>
            Wahoo ELEMNT BOLT GPS   ,     ,  
             . Wahoo ELEMNT BOLT GPS     ANT+,   
             .    ,     .    
                 ,      .
               ,      .  
                    .
          </p>

          <h3></h3>

          <p>
             Wahoo ELEMNT BOLT  .     74,6 x 47,3 x 22,1 .
                .      .   
                 56 .      , 
                   : ,  
            ,   .. (     ).      
             IPX7.  ,     ,     ( 30 )
                   1 .
          </p>
        </div>
      </div>
    </section>
  </main>

</template>

<script>
import ProductCount from '@/components/product/ProductCount'
import Preloader from '@/components/common/Preloader'
import numberFormat from '@/helpers/numberFormat'
import axios from 'axios'
import { API_BASE_URL } from '@/config'
import { mapActions } from 'vuex'
import ColorsList from '@/components/common/ColorsList'

export default {
  data () {
    return {
      productAmount: 1,
      productLoading: false,
      currentColor: '',

      productAdded: false,
      productAddSending: false
    }
  },
  components: {
    ProductCount,
    Preloader,
    ColorsList
  },
  methods: {
    ...mapActions(['addProductToCart']),
    addToCart () {
      this.addProductToCart({
        productId: this.product.id,
        amount: this.productAmount
      })
        .then(() => { //eslint-disable-line
          this.productAdded = true
          this.productAddSending = false
        })
    },
    loadProduct () {
      this.productLoading = true
      clearTimeout(this.loadProductsTimer)
      axios
        .get(API_BASE_URL + 'api/products/' + this.$route.params.id)
        .then(response => this.$store.commit('takeProduct', response.data))
        .catch(() => this.$router.replace({ name: 'notFound' })) //eslint-disable-line
        .then(() => this.productLoading = false) //eslint-disable-line
    }
  },
  filters: {
    numberFormat
  },
  computed: {
    product () {
      return this.$store.state.productData
    },
    categories () {
      return this.$store.state.productData.category
    }
  },
  watch: {
    '$route.params.id': {
      handler () {
        this.loadProduct()
      },
      immediate: true
    }
  }
}
</script>
<style scoped>
main {
  min-height: 100vh;
}
</style>
